___CSS_LOADER_EXPORT___.push([module.id, `.coordinate,
.coordinate-state {
  border-color: gray;
  border-width: 1px;
  border-style: solid;
  display: flex;
  align-items: center;
  justify-content: center;
}

#player1-board .coordinate--ship,
.coordinate-state--ship {
  border-color: rgb(69, 69, 255);
  background-color: rgb(195, 195, 255);
}



#player2-board .coordinate:hover {
  background-color: rgb(196, 196, 196);
  cursor: pointer;
}

#player2-board .coordinate--miss:hover {
  cursor: not-allowed;
}

#player2-board .coordinate--hit:hover {
  cursor: not-allowed;
  background-color: rgb(255, 145, 145);
}

#player1-board .coordinate--ship.coordinate--hit,
#player2-board .coordinate--ship.coordinate--hit,
#player2-board .coordinate-state--ship.coordinate-state--hit {
  border-color: rgb(255, 80, 80);
  background-color: rgb(255, 179, 179);
}

#player1-board .coordinate--ship.coordinate--sunk {
  border-color: black;
  background-color: rgb(255, 153, 0);
}

#player2-board .coordinate--ship.coordinate--sunk,
#player2-board .coordinate-state--ship.coordinate-state--sunk{
  border-color: black;
  background-color: rgb(255, 153, 0);
}

.miss-mark {
  inline-size: 20%;
  block-size: 20%;
  background-color: black;
  border-radius: 50%;
}`, "",{"version":3,"sources":["webpack://./src/css/coordinate.css"],"names":[],"mappings":"AAAA;;EAEE,kBAAkB;EAClB,iBAAiB;EACjB,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;;EAEE,8BAA8B;EAC9B,oCAAoC;AACtC;;;;AAIA;EACE,oCAAoC;EACpC,eAAe;AACjB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,oCAAoC;AACtC;;AAEA;;;EAGE,8BAA8B;EAC9B,oCAAoC;AACtC;;AAEA;EACE,mBAAmB;EACnB,kCAAkC;AACpC;;AAEA;;EAEE,mBAAmB;EACnB,kCAAkC;AACpC;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,uBAAuB;EACvB,kBAAkB;AACpB","sourcesContent":[".coordinate,\n.coordinate-state {\n  border-color: gray;\n  border-width: 1px;\n  border-style: solid;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n#player1-board .coordinate--ship,\n.coordinate-state--ship {\n  border-color: rgb(69, 69, 255);\n  background-color: rgb(195, 195, 255);\n}\n\n\n\n#player2-board .coordinate:hover {\n  background-color: rgb(196, 196, 196);\n  cursor: pointer;\n}\n\n#player2-board .coordinate--miss:hover {\n  cursor: not-allowed;\n}\n\n#player2-board .coordinate--hit:hover {\n  cursor: not-allowed;\n  background-color: rgb(255, 145, 145);\n}\n\n#player1-board .coordinate--ship.coordinate--hit,\n#player2-board .coordinate--ship.coordinate--hit,\n#player2-board .coordinate-state--ship.coordinate-state--hit {\n  border-color: rgb(255, 80, 80);\n  background-color: rgb(255, 179, 179);\n}\n\n#player1-board .coordinate--ship.coordinate--sunk {\n  border-color: black;\n  background-color: rgb(255, 153, 0);\n}\n\n#player2-board .coordinate--ship.coordinate--sunk,\n#player2-board .coordinate-state--ship.coordinate-state--sunk{\n  border-color: black;\n  background-color: rgb(255, 153, 0);\n}\n\n.miss-mark {\n  inline-size: 20%;\n  block-size: 20%;\n  background-color: black;\n  border-radius: 50%;\n}"],"sourceRoot":""}]);
